import { HandleError } from './../../shared/services/http-error-handler.service';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit, OnDestroy, Pipe, PipeTransform, NgZone, Renderer2, ElementRef, AfterViewInit } from '@angular/core';
import { trigger, state, style, animate, transition, keyframes } from '@angular/animations';


import { Blog } from 'models/blog';
import { BlogService } from 'content/shared/services/blog/blog.service';
import { Subscription } from 'rxjs';



@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.scss'],
  animations: [
    trigger('slideInUp', [
      state('in', style({ transform: 'translateX(0)' })),
      transition('void => *', [
        style({ transform: 'translateY(100%)' }),
        animate('300ms ease-in')
      ]),
      transition('* => void', [
        animate(100, style({ transform: 'translateY(-100%)' }))
      ])
      ])
    ]
})



export class BlogViewComponent implements OnInit, OnDestroy, AfterViewInit {

  blogs$: Blog[];
  blogs: Blog[];
  subscription: Subscription;
  private handleError: HandleError;
  public slideInUpState: string;
  items: any;

  constructor(
    private blogsService: BlogService,

    public zone: NgZone,
    private renderer: Renderer2,
    private el: ElementRef
  ) {   }


  ngOnInit() {
    this.subscription = this.viewBlog();
    console.log(this.subscription);
    this.triggerAnimation();
  }
  ngAfterViewInit() {
    // this.renderer.setAttribute(
    //   this
    // )
  }

  viewBlog() {
    return this.blogsService.viewBlog()
    .subscribe((data: Blog[]) => this.blogs = {
    ... data});
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }


  triggerAnimation() {
    this.slideInUpState = 'active';
  }

  reset() {
    this.zone.run(() => {
      this.slideInUpState = 'inactive';
   });
  }
}
