import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { HttpClient, HttpParams } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { HttpErrorHandler, HandleError } from '../http-error-handler.service';

// Models
import { Blog } from 'models/blog';


// rxjs
import { Observable } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { map } from 'rxjs/operators';

import { blogger_v3, GoogleApis } from 'googleapis';

import * as google from 'googleapis';

const httpOptions = {
  headers: new HttpHeaders({
    'Content-Type': 'application/json',
    'Authorization': 'my-auth-token'
  })
};



@Injectable()
export class BlogService {

  bloggerAPItest = 'https://www.googleapis.com/blogger/v3/blogs/7337281974763391047/posts?key=AIzaSyDrcjlSJFv_e0IN6ywJJ6nrCEcWTJhcl-c';
  bloggerUrl = 'https://www.googleapis.com/blogger/v3/blogs/byurl?url=http://electricgrip.blogspot.com/';
  bloggerAPI = 'https://www.googleapis.com/blogger/v3/blogs/1115640269623940299/posts?key=AIzaSyDlaPrWE4JALFPqeFiVuCWdBEUpH1lZ_eA';

  private handleError: HandleError;
  blogs$: Observable<Blog[]>;
  blog: any;


  constructor(
    private http: HttpClient,
    httpErrorHandler: HttpErrorHandler
  ) {
    this.handleError = httpErrorHandler.createHandleError('BlogService');

  }
    viewBlog() {
      return this.http.get<Blog[]>(this.bloggerAPI);
    }

    addBlog(blog: Blog) {
      return this.http.post<Blog>(this.bloggerAPI, blog, httpOptions)
        .pipe(
          catchError(this.handleError('addBlog', blog))
          );
    }
}
