import { Component, OnInit, Input, Pipe } from '@angular/core';
import { Blog } from 'models/blog';
// import { pipe } from '@angular/core/src/render3/pipe';
// import { DomSanitizer, SafeHtml, SafeStyle, SafeScript, SafeUrl, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-replace',
  templateUrl: './replace.component.html',
  styleUrls: ['./replace.component.scss']
})
export class ReplaceComponent implements OnInit {

  @Input()
  blog: Blog;

  @Pipe({ name: 'safe' })

  modalStart: string;
  moadlEnd: string;
  id: string;

  contentReplace: string;

  constructor() {



   }

  ngOnInit() {
 
    // this.contentReplace = this.blog.content.replace(/<a/, this.modalStart + '<a #id ').replace(/a>/, 'a>' + this.moadlEnd);
    console.log();
  }
  ModalStart() {
    this.id = this.blog.title.replace(/ /g, '');
    const contentReplace = this.blog.content.replace(/<a href/g, '<a data-toggle="modal" data-target="#' + this.id + '" href');
    return contentReplace;
  }
  ModalDisplay() {
    this.id = this.blog.title.replace(/ /g, '');
    const modalStart = `
<div class="modal fade" id="` + this.id + `" role="dialog" 
tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered">
    <div class="modal-body">
      <div class="container-fluid">
`;
    const moadlEnd = `
      </div>
    </div>
  </div>
</div>
`;
    const Modal = this.blog.content
    .replace(/href="/g, '  ')
    .replace(/<img /g, modalStart + '<img width="100%"').replace(/<\/a>/g, moadlEnd + '');
    return Modal;
  }
}
