import { BlogModule } from './content/blog/blog.module';
import { BlogViewComponent } from 'content/blog/view/blog-view.component';
import { HomeComponent } from './content/home/home.component';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { PhotoGalleryComponent } from 'content/photo-gallery/photo-gallery.component';

const routes: Routes = [
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'blogView', component: BlogViewComponent },
  { path: 'photo_gallery', component: PhotoGalleryComponent },
  { path: 'blog', loadChildren: () => BlogModule },
  { path: '**', redirectTo: 'home' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { enableTracing: false, scrollPositionRestoration: 'enabled' })],
  exports: [RouterModule]
})

export class AppRoutingModule {
    ModuleWithProviders = RouterModule.forRoot(routes);
}
