import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { BlogComponent } from './container/blog/blog.component';
import { BlogsComponent } from './container/blogs/blogs.component';
import { BlogFormComponent } from './components/blog-form/blog-form.component';
import { BlogService } from 'content/shared/services/blog/blog.service';
import { RouterModule, Routes } from '@angular/router';
import { ReplaceComponent } from './view/replace/replace.component';
import { PipesComponent } from 'pipes/pipes.component';


export const ROUTES: Routes = [
  { path: '', component: BlogsComponent },
  { path: ':id', component: BlogComponent },
  { path: 'new', component: BlogComponent }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(ROUTES)
  ],
  declarations: [
    BlogComponent,
    BlogsComponent,
    BlogFormComponent,
    ReplaceComponent,
  PipesComponent],
  providers: [
    BlogService,
    RouterModule
  ],
  exports: [
    ReplaceComponent
  ]
})
export class BlogModule { }
